import { ExdComponent } from "../exd/exd.component";
import { LabelComponent } from "../label/label.component";
import { ChartComponent } from "../chart/chart.component";
import { UnknownComponent } from "../unknown/unknown.component";
import { MatrixComponent } from "../matrix/matrix.component";
import { DashComponent } from "../dash/dash.component";

const componentType = {
  EXD: {
    componentFactory: ExdComponent
  },
  LAB: {
    componentFactory: LabelComponent
  },
  CHA: {
    componentFactory: ChartComponent
  },
  MAT: {
    componentFactory: MatrixComponent
  },
  DSH: {
    componentFactory: DashComponent
  },
  UNK: {
    componentFactory: UnknownComponent
  }
};

export function getComponentByType(type: string) {
  const comp = componentType[type];

  return comp ? comp : componentType.UNK;
}
