import { Component } from "@angular/core";
import { Store } from "@ngrx/store";

import { BasicComponent } from "../basic-component";
import { getStyle } from "../section/section.helper";

import { State } from "src/app/store";

@Component({
  selector: "smeup-exd",
  templateUrl: "./exd.component.html",
  styleUrls: ["./exd.component.scss"]
})
export class ExdComponent extends BasicComponent {
  readonly componentType = "EXD";

  constructor(store: Store<State>) {
    super(store);
  }

  get exdClass() {
    if (!this.comp) {
      return null;
    }

    return {
      row: "column" !== this.comp.layout
    };
  }

  getSectionStyle(section: any) {
    return getStyle(section);
  }
}
