import { Injectable } from "@angular/core";
import { of } from "rxjs";
import { mergeMap, map, catchError } from "rxjs/operators";
import { Actions, createEffect, ofType } from "@ngrx/effects";

import { ScriptService } from "../../services/script.service";
import * as GrafActions from "./graf.actions";
import ExpressionEvaluator from "../../models/classes/expressions/ExpressionEvaluator";

@Injectable()
export class GrafEffects {
  loadRoot$ = createEffect(() =>
    this.actions$.pipe(
      ofType(GrafActions.loadRoot),
      mergeMap(() =>
        this.scriptService.getScript("config/graf/Start3").pipe(
          map(root => GrafActions.loadRootSuccess({ root })),
          catchError(message => of(GrafActions.loadRootFail({ message })))
        )
      )
    )
  );

  loadComponent$ = createEffect(() =>
    this.actions$.pipe(
      ofType(GrafActions.loadComponent),
      mergeMap(({ comp }) => {
        const fun = new ExpressionEvaluator().variableExpression(
          comp,
          comp.fun
        );

        return this.scriptService.getScript(fun).pipe(
          map(data => GrafActions.loadComponentSuccess({ data, id: comp.id })),
          catchError(message =>
            of(GrafActions.loadComponentFail({ id: comp.id, message }))
          )
        );
      })
    )
  );

  constructor(
    private actions$: Actions,
    private scriptService: ScriptService
  ) {}
}
