import { Component, OnInit, OnDestroy } from "@angular/core";
import { Store, select } from "@ngrx/store";
import { MatSnackBar } from "@angular/material/snack-bar";

import * as GrafActions from "./store/graf/graf.actions";
import { State, getRootComponent, getMessages } from "./store";
import { Observable, Subscription } from "rxjs";

@Component({
  selector: "app-root",
  templateUrl: "./app.component.html",
  styleUrls: ["./app.component.scss"]
})
export class AppComponent implements OnInit, OnDestroy {
  root$: Observable<any>;

  private messagesSub: Subscription;

  constructor(private store: Store<State>, private snackBar: MatSnackBar) {}

  ngOnInit() {
    this.store.dispatch(GrafActions.loadRoot());

    this.root$ = this.store.pipe(select(getRootComponent));

    this.messagesSub = this.store
      .pipe(select(getMessages))
      .subscribe(messages =>
        messages.forEach(message =>
          this.snackBar.open(message, "Close", {
            duration: 2000
          })
        )
      );
  }

  ngOnDestroy() {
    if (this.messagesSub) {
      this.messagesSub.unsubscribe();
    }
  }
}
