import {
  ActionReducer,
  ActionReducerMap,
  createFeatureSelector,
  createSelector,
  MetaReducer
} from "@ngrx/store";

import { environment } from "../../environments/environment";

import * as fromGraf from "./graf/graf.reducers";
import * as fromMessages from "./messages/messages.reducers";

export interface State {
  graf: fromGraf.State;
  messages: fromMessages.State;
}

const selectComponents = (state: State) => state.graf.componentsById;
const selectRootComponentId = (state: State) => state.graf.rootComponentId;
const selectMessages = (state: State) => state.messages.messages;

export const getComponentById = createSelector(
  selectComponents,
  (components: any, props: { id: string }) => components[props.id]
);

export const getRootComponent = createSelector(
  selectComponents,
  selectRootComponentId,
  (components, rootComponentId) => components[rootComponentId]
);

export const getMessages = createSelector(
  selectMessages,
  messages => messages
);

export const reducers: ActionReducerMap<State> = {
  graf: fromGraf.reducer,
  messages: fromMessages.reducer
};

export const metaReducers: MetaReducer<State>[] = !environment.production
  ? []
  : [];
