interface Variable {
  key: string;
  value: string;
}

export interface VariableMap {
  [index: string]: string;
}

interface Basic {
  id: string;
  component: {
    title?: string;
  };
}

export default class Dynamism {
  event: string;
  source?: Basic;
  variables: VariableMap = {};
  targets: string[] = [];
  title?: string;
  exec: string = "";

  constructor(event: string) {
    this.event = event;
  }

  addImplictVariable(variable: Variable) {
    this.variables[variable.key] = variable.value;
  }

  isEnabled() {
    // TODO
    return true;
  }
}
