import { createAction, props } from "@ngrx/store";

import { VariableMap } from "../../models/classes/Dynamism";

export const loadRoot = createAction("[Graf] Load Root");

export const loadRootSuccess = createAction(
  "[Graf] Load Root Success",
  props<{ root: any }>()
);

export const loadRootFail = createAction(
  "[Graf] Load Root Fail",
  props<{ message: string }>()
);

export const loadComponent = createAction(
  "[Graf] Load Component",
  props<{ comp: any }>()
);

export const loadComponentSuccess = createAction(
  "[Graf] Load Component Success",
  props<{ data: any; id: string }>()
);

export const loadComponentFail = createAction(
  "[Graf] Load Component Fail",
  props<{ id: string; message: string }>()
);

export const addComponent = createAction(
  "[Graf] Add Component",
  props<{ comp: any }>()
);

export const addVariables = createAction(
  "[Graf] Add Variables",
  props<{ id: string; variables: VariableMap }>()
);

export const removeComponent = createAction(
  "[Graf] Remove Component",
  props<{ id: string }>()
);
