import { Component } from "@angular/core";
import { Store } from "@ngrx/store";

import { BasicComponent } from "../basic-component";

import { State } from "src/app/store";
import { DynamismService } from "src/app/services/dynamism.service";
import Dynamism from "src/app/models/classes/Dynamism";

@Component({
  selector: "smeup-matrix",
  templateUrl: "./matrix.component.html",
  styleUrls: ["./matrix.component.scss"]
})
export class MatrixComponent extends BasicComponent {
  readonly componentType = "MAT";

  constructor(store: Store<State>, private dynService: DynamismService) {
    super(store);
  }

  get columnsWidth() {
    const attr = this.getAttributeValue("columnsWidth");
    return attr ? attr : [];
  }

  get enableSortableColumns(): boolean {
    return !!this.getAttributeValue("enableSortableColumns");
  }

  get expandGroups() {
    return !!this.getAttributeValue("expandGroups");
  }

  get filters() {
    const attr = this.getAttributeValue("filters");
    return attr ? attr : {};
  }

  onRowSelected({ detail }) {
    const clickedColumn: string = detail.clickedColumn;
    const selectedRows: any[] = detail.selectedRows;

    const dyns = this.getDynamisms("click");

    dyns.forEach(dyn => {
      const d = new Dynamism(dyn.event);
      d.source = this.comp;
      d.targets = dyn.targets;

      d.addImplictVariable({
        key: "Column",
        value: clickedColumn
      });

      if (selectedRows.length === 1) {
        const row = selectedRows[0];

        const selectedCell = row.cells[clickedColumn];

        d.addImplictVariable({
          key: "T1",
          value: selectedCell.obj.t
        });
        d.addImplictVariable({
          key: "P1",
          value: selectedCell.obj.p
        });
        d.addImplictVariable({
          key: "K1",
          value: selectedCell.obj.k
        });

        Object.keys(row.cells).forEach(k => {
          d.addImplictVariable({
            key: k,
            value: row.cells[k].obj.k
          });
        });
      } else {
        // TOOD Multi sel
      }

      this.dynService.execute(d);
    });
  }
}
