import {
  Column,
  Row,
  Cell
} from "src/assets/dist/types/components/kup-data-table/kup-data-table-declarations";

interface GrafanaSerie {
  columns: string[];
  name: string;
  values: any[][];
}

interface GrafanaResult {
  series: GrafanaSerie[];
  statement_id: number;
}

export interface GrafanaData {
  results: GrafanaResult[];
}

export function transform(grafanaData: GrafanaData) {
  const data = {
    columns: [],
    rows: []
  };

  if (!grafanaData) {
    return data;
  }

  const firstResult = grafanaData.results[0];

  const firstSerie = firstResult.series[0];

  data.columns = convertColumns(firstSerie.columns);

  data.rows = firstSerie.values.map(row => convertRow(firstSerie.columns, row));

  return data;
}

function convertColumns(grafanaColumns: string[]): Column[] {
  return grafanaColumns.map(name => ({
    name,
    title: name
  }));
}

function convertRow(grafanaColumns: string[], grafanaRow: any[]): Row {
  const r: Row = {
    cells: {}
  };

  for (let i = 0; i < grafanaColumns.length; i++) {
    const column = grafanaColumns[i];

    const cell: Cell = {
      obj: {
        t: "",
        p: "",
        k: grafanaRow[i]
      },
      value: grafanaRow[i]
    };

    r.cells[column] = cell;
  }

  return r;
}
