import { Injectable } from "@angular/core";
import { HttpClient, HttpErrorResponse } from "@angular/common/http";

import { throwError, of, Observable } from "rxjs";
import { retry, catchError, map } from "rxjs/operators";

import { environment } from "src/environments/environment";
import { getChartData } from "src/mocks/dynamicChart";
import { GrafanaData, transform } from "src/transformers/grafanaTransformer";

@Injectable({
  providedIn: "root"
})
export class ScriptService {
  constructor(private http: HttpClient) {}

  getScript(script: string): Observable<any> {
    // mock for dynamic charts... to be removed
    if ("dynamic/graf/chart" === script) {
      return of(getChartData());
    } else if (script.startsWith("graf:")) {
      const url = script.substr(5);

      return this.http.get(`${environment.grafanaUrl}/${url}`).pipe(
        retry(3),
        map((data: GrafanaData) => transform(data)),
        catchError(this.handleError)
      );
    } else {
      return this.http.get(`${environment.configUrl}/${script}`).pipe(
        retry(3),
        catchError(this.handleError)
      );
    }
  }

  private handleError(error: HttpErrorResponse) {
    if (error.error instanceof ErrorEvent) {
      // A client-side or network error occurred. Handle it accordingly.
      console.error("An error occurred:", error.error.message);
    } else {
      // The backend returned an unsuccessful response code.
      // The response body may contain clues as to what went wrong,
      console.error(
        `Backend returned code ${error.status}, body was:`,
        error.error
      );
    }
    // return an observable with a user-facing error message
    return throwError("Something bad happened; please try again later.");
  }
}
