import { OnDestroy, OnInit, Input } from "@angular/core";
import { Store, select } from "@ngrx/store";
import { Subscription, interval } from "rxjs";

import { State, getComponentById } from "../store";
import * as GrafActions from "../store/graf/graf.actions";

export abstract class BasicComponent implements OnInit, OnDestroy {
  @Input() id: string;

  abstract readonly componentType: string;

  private storeSub: Subscription;
  private refreshSub: Subscription;

  comp: any;

  constructor(private store: Store<State>) {}

  ngOnInit() {
    this.storeSub = this.store
      .pipe(select(getComponentById, { id: this.id }))
      .subscribe(comp => {
        this.comp = comp;
        this.loadData();
        this.checkRefreshSub();
      });
  }

  ngOnDestroy() {
    this.store.dispatch(GrafActions.removeComponent({ id: this.id }));

    this.storeSub.unsubscribe();

    if (this.refreshSub) {
      this.refreshSub.unsubscribe();
    }
  }

  getComponentType() {
    return this.componentType;
  }

  private loadData() {
    if (!this.comp || !this.comp.loaded) {
      return;
    }

    if (this.componentType === "EXD") {
      if (this.comp.sections) {
        // comp already loaded
        return;
      }
    } else {
      if (this.comp.data) {
        // data already loaded, nothing to do
        return;
      }
    }

    this.store.dispatch(
      GrafActions.loadComponent({
        comp: this.comp
      })
    );
  }

  protected getAttributeValue(attribute: string): any {
    const ct = this.getComponentType();

    if (!this.comp || !this.comp.options || !ct || !attribute) {
      return null;
    }

    const options = this.comp.options[ct];

    if (!options) {
      console.warn(
        `cannot find a setup for component type ${ct}`,
        this.comp.options
      );
      return null;
    }

    const defaultSetup = options.default;

    if (!defaultSetup) {
      console.warn("cannot find a default setup", options);
      return null;
    }

    return defaultSetup[attribute];
  }

  protected hasDynamisms(): boolean {
    return (
      this.comp != null && this.comp.dynamisms && this.comp.dynamisms.length > 0
    );
  }

  protected getDynamisms(dynamismName: string): any[] {
    if (this.hasDynamisms()) {
      return this.comp.dynamisms.filter(
        (dynamism: any) => dynamismName === dynamism.event
      );
    }
    return [];
  }

  private checkRefreshSub() {
    if (this.refreshSub) {
      this.refreshSub.unsubscribe();
    }

    const refresh = this.getAttributeValue("refresh");
    if (refresh) {
      this.refreshSub = interval(parseInt(refresh)).subscribe(() =>
        this.store.dispatch(
          GrafActions.loadComponent({
            comp: this.comp
          })
        )
      );
    }
  }
}
