import { Component } from "@angular/core";
import { Store } from "@ngrx/store";

import { BasicComponent } from "../basic-component";

import { State } from "src/app/store";

@Component({
  selector: "smeup-chart",
  templateUrl: "./chart.component.html",
  styleUrls: ["./chart.component.scss"]
})
export class ChartComponent extends BasicComponent {
  readonly componentType = "CHA";

  constructor(store: Store<State>) {
    super(store);
  }

  get types(): string[] {
    return this.getAttributeValue("types");
  }

  getAxis(): string {
    return this.getAttributeValue("axis");
  }

  getSeries(): string[] {
    return this.getAttributeValue("series");
  }
}
