function randomNumberFromInterval(min: number, max: number) {
  // min and max included
  return Math.floor(Math.random() * (max - min + 1) + min);
}

const data = {
  columns: [
    {
      name: "Col1",
      title: "Person",
      size: "10"
    },
    {
      name: "Col2",
      title: "Value1",
      size: "10"
    },
    {
      name: "Col3",
      title: "Value2",
      size: "10"
    }
  ],
  rows: []
};

export const getChartData = () => {
  const rows = data.rows;

  if (rows.length <= 10) {
    while (rows.length <= 10) {
      // creating a new row
      const col2Value = randomNumberFromInterval(50, 70);
      const col3Value = randomNumberFromInterval(50, 70);

      const newRow = {
        cells: {
          Col1: {
            obj: {
              t: "NR",
              p: "",
              k: rows.length
            },
            value: rows.length
          },
          Col2: {
            obj: {
              t: "NR",
              p: "",
              k: col2Value
            },
            value: col2Value
          },
          Col3: {
            obj: {
              t: "NR",
              p: "",
              k: col3Value
            },
            value: col3Value
          }
        }
      };

      rows.push(newRow);
    }
  } else {
    rows.splice(0, 1);

    const col2Value = randomNumberFromInterval(50, 70);
    const col3Value = randomNumberFromInterval(50, 70);

    const newRow = {
      cells: {
        Col1: {
          obj: {
            t: "NR",
            p: "",
            k: rows[rows.length - 1].cells.Col1.value + 1
          },
          value: rows[rows.length - 1].cells.Col1.value + 1
        },
        Col2: {
          obj: {
            t: "NR",
            p: "",
            k: col2Value
          },
          value: col2Value
        },
        Col3: {
          obj: {
            t: "NR",
            p: "",
            k: col3Value
          },
          value: col3Value
        }
      }
    };

    rows.push(newRow);
  }

  return { columns: [...data.columns], rows: [...rows] };
};
