import {
  Component,
  ComponentFactoryResolver,
  Input,
  OnInit,
  ViewChild
} from "@angular/core";
import { Store, select } from "@ngrx/store";
import { take } from "rxjs/operators";

import { ChooseComponentDirective } from "../../directives/choose-component.directive";
import { BasicComponent } from "../basic-component";
import { getComponentByType } from "./choose-component-utils";
import { State, getComponentById } from "src/app/store";

@Component({
  selector: "smeup-choose",
  templateUrl: "./choose.component.html",
  styleUrls: ["./choose.component.scss"]
})
export class ChooseComponent implements OnInit {
  @Input() id: string;

  @ViewChild(ChooseComponentDirective, { static: true })
  host: ChooseComponentDirective;

  constructor(
    private store: Store<State>,
    private componentFactoryResolver: ComponentFactoryResolver
  ) {}

  ngOnInit() {
    this.store
      .pipe(
        take(1),
        select(getComponentById, { id: this.id })
      )
      .subscribe(comp => {
        if (!comp) {
          return;
        }

        const type: string = comp.type;
        const c = getComponentByType(type);

        const componentFactory = this.componentFactoryResolver.resolveComponentFactory(
          c.componentFactory
        );

        // clearing content of template
        const viewContainerRef = this.host.viewContainerRef;
        viewContainerRef.clear();

        // create component
        const componentRef = viewContainerRef.createComponent(componentFactory);
        (<BasicComponent>componentRef.instance).id = comp.id;
      });
  }
}
