import { Component, Input, OnInit } from "@angular/core";
import { Store } from "@ngrx/store";
import { State } from "src/app/store";
import { addComponent } from "src/app/store/graf/graf.actions";
import { Section } from "src/app/models/interfaces/comp/section";
import { getStyle } from "./section.helper";

@Component({
  selector: "smeup-section",
  templateUrl: "./section.component.html",
  styleUrls: ["./section.component.scss"]
})
export class SectionComponent implements OnInit {
  @Input() section: Section;

  constructor(private store: Store<State>) {}

  ngOnInit() {
    if (this.hasComponents) {
      this.section.components.forEach(comp =>
        this.store.dispatch(addComponent({ comp }))
      );
    }
  }

  get isFlat() {
    if (this.hasSections) {
      return false;
    }

    return this.hasComponents && this.section.components.length === 1;
  }

  get mainComponent(): any | null {
    if (this.isFlat) {
      return this.section.components[0];
    }

    return null;
  }

  get sectionClass() {
    if (!this.section) {
      return null;
    }

    return {
      section: true,
      row: "column" !== this.section.layout
    };
  }

  get hasSections(): boolean {
    return (
      !!this.section &&
      !!this.section.sections &&
      this.section.sections.length > 0
    );
  }

  get hasComponents(): boolean {
    return (
      !!this.section &&
      !!this.section.components &&
      this.section.components.length > 0
    );
  }

  getSectionStyle(section: Section) {
    return getStyle(section);
  }
}
