import { Injectable } from "@angular/core";
import { Store } from "@ngrx/store";

import { State } from "../store";
import Dynamism from "../models/classes/Dynamism";
import * as GrafActions from "../store/graf/graf.actions";

@Injectable({
  providedIn: "root"
})
export class DynamismService {
  constructor(private store: Store<State>) {}

  execute(dynamism: Dynamism) {
    if (!dynamism.source) {
      // TODO errore
      return;
    }

    if (!dynamism.isEnabled()) {
      // TODO
      return;
    }

    // check targets
    if (!dynamism.targets || dynamism.targets.length == 0) {
      // save variable in source
      this.store.dispatch(
        GrafActions.addVariables({
          id: dynamism.source.id,
          variables: dynamism.variables
        })
      );
    } else {
      dynamism.targets.forEach(target => {
        this.store.dispatch(
          GrafActions.addVariables({
            id: target,
            variables: dynamism.variables
          })
        );
      });
    }

    if (
      dynamism.targets.length === 0 &&
      dynamism.exec &&
      dynamism.exec !== ""
    ) {
      // TODO exec?
    }
  }
}
