import { Action, createReducer, createSelector, on } from "@ngrx/store";

import * as GrafActions from "./graf.actions";

interface GenericMap {
  [index: string]: any;
}

export interface State {
  componentsById: GenericMap;
  rootComponentId: string;
}

export const initialState: State = {
  componentsById: {},
  rootComponentId: null
};

const grafReducer = createReducer(
  initialState,

  on(GrafActions.loadRootSuccess, (state, { root }) => {
    const newCompById = { ...state.componentsById };
    newCompById[root.id] = root;

    return {
      ...state,
      componentsById: newCompById,
      rootComponentId: root.id
    };
  }),

  on(GrafActions.loadRootFail, state => ({
    ...state,
    componentsById: {},
    rootComponentId: null
  })),

  on(GrafActions.loadComponentSuccess, (state, { data, id }) => {
    const copy = { ...state, componentsById: { ...state.componentsById } };

    const comp = copy.componentsById[id];

    if (comp) {
      if ("EXD" === comp.type) {
        copy.componentsById[id] = {
          ...copy.componentsById[id],
          sections: data
        };
      } else {
        copy.componentsById[id] = { ...copy.componentsById[id], data };
      }
    }

    return copy;
  }),

  on(GrafActions.addComponent, (state, { comp }) => {
    const copy = { ...state, componentsById: { ...state.componentsById } };

    copy.componentsById[comp.id] = comp;

    return copy;
  }),

  on(GrafActions.addVariables, (state, { id, variables }) => {
    const copy = { ...state, componentsById: { ...state.componentsById } };

    const compToUpdate = copy.componentsById[id];

    if (compToUpdate) {
      const ct = compToUpdate.type;

      // TODO mmh not sure about this
      if ("EXD" === ct) {
        copy.componentsById[id] = {
          ...copy.componentsById[id],
          loaded: true,
          sections: null,
          variables: { ...compToUpdate.variables }
        };
      } else {
        copy.componentsById[id] = {
          ...copy.componentsById[id],
          loaded: true,
          data: null,
          variables: { ...compToUpdate.variables }
        };
      }

      Object.keys(variables).forEach(
        k => (copy.componentsById[id].variables[k] = variables[k])
      );
    }

    return copy;
  }),

  on(GrafActions.removeComponent, (state, { id }) => {
    const copy = { ...state, componentsById: { ...state.componentsById } };

    delete copy.componentsById[id];

    return copy;
  })
);

export function reducer(state: State | undefined, action: Action) {
  return grafReducer(state, action);
}
